import React from 'react';
import './drink.scss'
import { connect, batch } from 'react-redux';
import { showLoading } from '../app/app-actions'
import { usbAddMsgToDevice } from '../usb/usb-actions'
import { resetDrinks } from './drinks-actions'

const stateToProps = state => {
    return { drink: state.drinks.drink };
};

function dispatchToProps(dispatch) {
    return {
        chooseDrink: d => {
            // https://redux.js.org/faq/performance/#how-can-i-reduce-the-number-of-store-update-events
            batch(() => {
                dispatch(resetDrinks())
                dispatch(usbAddMsgToDevice(d.id))
                dispatch(showLoading())
            })
        }
    };
}

function Drink({ drink, chooseDrink }) {
    return (<div className='drink'>

        <div className='drink-icon'>
            <img className='drink-icon center'
                src={drink.icon}
                alt={drink.name}></img>
        </div>

        <section className='drink'>

            <h2 className='drink-name'>
                <span className='drink-name center-top-grh'>
                    {drink.name}
                </span>
            </h2>

            <div className='drink-ings'>
                <ul className='drink-ings center-v'>
                    {drink.ingredients
                        .map((val, idx, arr) => (
                            <li key={drink.id + '-s' + idx}
                                className='drink-ings' >
                                {val}
                            </li>)
                        )
                    }
                </ul>
            </div>

            <aside className='drink-btn'>
                <button className='center-top-grh'
                    onClick={() => chooseDrink(drink)}>
                    ZRÓB DRINKA
                </button>
            </aside>

        </section>
    </div>)
}

export default connect(stateToProps, dispatchToProps)(Drink);