import { createReducer } from '@reduxjs/toolkit'

import {
    showApp,
    showLoading,
    setAppMode
} from './app-actions'

// DISPLAY
export const APP_DISPLAY_DRINKER = 'APP_DISPLAY_DRINKER'
export const APP_DISPLAY_AUX = 'APP_DISPLAY_AUX'

// VIEW
export const APP_VIEW = 'APP_VIEW'
export const APP_LOADING_VIEW = 'APP_LOADING_VIEW'

// STATE
export const APP_STARTED_STATE = 'APP_STARTED_STATE'
export const APP_USB_GUI_CONNECTED_STATE = 'APP_USB_GUI_CONNECTED_STATE'
export const APP_USB_DEVICE_CONNECTED_STATE = 'APP_USB_DEVICE_CONNECTED_STATE'
export const APP_INITIALIZED_STATE = 'APP_INITIALIZED_STATE'

// MODE
export const APP_NORMAL_MODE = 'APP_NORMAL_MODE'
export const APP_DASHBOARD_MODE = 'APP_LOGS_MODE'
export const APP_DASHBOARD_FULL_SCREEN_MODE = 'APP_DASHBOARD_FULL_SCREEN_MODE'

const initialState = {
    display: APP_DISPLAY_DRINKER,
    // display: APP_DISPLAY_AUX,
    //
    // view: APP_LOADING_VIEW,
    view: APP_VIEW,
    //
    state: APP_STARTED_STATE,
    //
    // mode: APP_NORMAL_MODE
    mode: APP_DASHBOARD_MODE
    // mode: APP_DASHBOARD_FULL_SCREEN_MODE
}

// https://redux-toolkit.js.org/api/createreducer/
const appReducer = createReducer(initialState, {
    [showApp]: (state, action) => {
        state.view = APP_VIEW
    },

    [showLoading]: (state, action) => {
        state.view = APP_LOADING_VIEW
    },

    [setAppMode]: (state, action) => {
        state.mode = action.payload
    }
})

export default appReducer;