//     // our middleware can have access to the store

//     return function (next) {
//         // next is the following action to be run
//         // after this middleware

//         return function (action) {
//             // finally, this is where our logic lives for
//             // our middleware.
//         }
//     }
// }

const loggingMiddleware = store => next => action => {
    if (action.type === usbMsgFromPlugin.type && action.payload) {
        try {
            const msg = json.fromjson(action.payload)
            log.log(`_:action = ` + action.type
                + '\r\n' + msg.time + ' ' + msg.type + ':'
                + '\r\n' + msg.data)
        } catch (e) {
            log.err(e)
            log.err(`::action = ` + action.type
                + ' \r\n action.payload = ' + action.payload)
        }
    } else {
        log.log(`::action = ` + action.type
            + ' \r\n' + json.tojson(action.payload))
    }

    // call the next function
    next(action);
}

export default loggingMiddleware;