import React from 'react'
import './category.scss'
import DrinksList from '../drinks/DrinksList'
import DrinksAlco from '../drinks/DrinksAlco'
import DrinksCustom from '../drinks/DrinksCustom'
import DrinksRandom from '../drinks/DrinksRandom'
import DrinksTop from '../drinks/DrinksTop'

import { connect, batch } from 'react-redux'
import { showCategories } from './categories-actions'
import { resetDrinks } from '../drinks/drinks-actions'

const drinksViews = {
    DrinksList,
    DrinksAlco,
    DrinksCustom,
    DrinksRandom,
    DrinksTop
}

const stateToProps = state => {
    const category = state.categories.category

    return {
        category,
        DrinksView: drinksViews[category.id]
    };
};

function dispatchToProps(dispatch) {
    return {
        showCategories: () => {
            batch(() => {
                dispatch(resetDrinks())
                dispatch(showCategories())
            })
        }
    };
}

function Category({ category, showCategories, DrinksView }) {
    return <div className='category '>

        <DrinksView />

        <aside className='category'>

            <h2 className='category' >
                <span className='category center-top-grl'>
                    {category.name}
                </span>
            </h2>

            <div className='category'>
                <button className='category center'
                    onClick={() => showCategories()}>
                    H
                </button>
            </div>

        </aside>

    </div>
}

export default connect(stateToProps, dispatchToProps)(Category);
