import { isFunction as isFun } from 'lodash'

const onReadSucClbks = {}
const onReadErrClbks = {}

const onReadSucClbk =
  msg => Object.values(onReadSucClbks)
    .forEach(c => c(msg))


const onReadErrClbk =
  msg => Object.values(onReadErrClbks)
    .forEach(c => c(msg))


const handleClbk =
  (clbks = {}, name = '', fn) => isFun(fn) ? clbks[name] = fn : false

let usb = null

function create() {

  return {
    getSucClbks: () => onReadSucClbks,

    getErrClbks: () => onReadErrClbks,

    connectGui: usbPlugin => {

      if (!usbPlugin) {
        throw new Error('Cannot create tmkusb')
      }

      usb = usbPlugin

      usb.connectGui(onReadSucClbk, onReadErrClbk)
    },

    addOnReadSucClbk: (name = '', fn) => {
      handleClbk(onReadSucClbks, name, fn)
    },

    remOnReadSucClbk: name => delete onReadSucClbks[name],

    addOnReadErrClbk: (name = '', fn) => {
      handleClbk(onReadErrClbks, name, fn)
    },

    remOnReadErrClbk: name => delete onReadErrClbks[name],

    exec: (name = '', msg = '') => {
      return new Promise((resolve, reject) => {
        msg.length
          ? usb[name](msg, resolve, reject)
          : usb[name](resolve, reject)
      })
    }
  }
}

const instance = create()

export {
  instance as default,
  create
}