import React from 'react'
import './app-loading.scss'
import { connect } from 'react-redux'
import { APP_LOADING_VIEW } from './app-reducer'

const stateToProps = state => {
    return { view: state.app.view };
};

function AppLoading({ view }) {
    // Math.random - makes loading gif each time from the beginning
    const gifSrc = view === APP_LOADING_VIEW
        ? 'intro.gif?a=' + Math.random()
        : ''

    return (<img id="app-loading" className='center' src={gifSrc} alt='loading' ></img>)
}

export default connect(stateToProps)(AppLoading);