import React from 'react'
import './categories.scss'
import { connect } from 'react-redux'
// import { CATEGORY_VIEW, CATEGORIES_VIEW } from './categories-reducer'
import CategoriesChoices from './Categories'
import Category from './Category'

const views = {
    CATEGORY_VIEW: Category,
    CATEGORIES_VIEW: CategoriesChoices
}

const stateToProps = state => {
    return {
        View: views[state.categories.view]
    };
};

function Categories({ View }) {
    return <View />
}

export default connect(stateToProps)(Categories);
