import _ from 'lodash'

let counter = 0
let msgFromGui = 'none'

export default {

    connectGui: function (successCallback, errorCallback) {
        function timeout() {
            setTimeout(function () {
                if (counter === 0) {
                    successCallback('guiConnected')
                }

                if (counter === 1) {
                    successCallback('deviceConnected')
                }

                if (counter === 2) {
                    successCallback('setData')
                }

                if (counter % 3 === 0) {
                    successCallback('getData')
                }

                if (counter % 9 === 0) {
                    if (msgFromGui !== 'none') {
                        successCallback('drinkDone')
                        msgFromGui = 'none'
                    }
                }

                counter++

                timeout()
            }, _.random(1000000, 1000000))
        }

        timeout()
    },

    init: function (successCallback, errorCallback) {
        setTimeout(function () {
            successCallback('initialized')
        }, _.random(1000, 3000))
    },

    write: function (text, successCallback, errorCallback) {
        console.log('msg from the gui: ' + text)
        msgFromGui = text
        successCallback()

    },

    resetConfig: function (successCallback, errorCallback) {
        setTimeout(function () {
            successCallback('resetConfig')
        }, _.random(1000, 3000))
    },

    getConfig: function (successCallback, errorCallback) {
        setTimeout(function () {
            successCallback('getConfig')
        }, _.random(1000, 3000))
    },

    getLogs: function (successCallback, errorCallback) {
        setTimeout(function () {
            successCallback(['getLogs'])
        }, _.random(1000, 3000))
    },

    reset: function (successCallback, errorCallback) {
        setTimeout(function () {
            successCallback('reset')
        }, _.random(1000, 3000))
    }

}