import React from 'react';
import './drinks-list-item.scss'

function DrinksListItem({ drink }) {

    return (<div className='drinks-list-item'>

        <h2 className='drinks-list-item'>
            <span className='center-top-grh'>
                {drink.name}
            </span>
        </h2>

        <section className='drinks-list-item'>
            <p className='center-top-grl'>
                {drink.ingredients
                    .map((val, idx, arr) => {
                        const spacer = idx < arr.length - 1
                            ? <em key={drink.id + '-e' + idx} className='drinks-list-item' >&nbsp;</em>
                            : ''

                        return (<span key={drink.id + '-s' + idx} className='drinks-list-item'>
                            {val}{spacer}
                        </span>)
                    })
                }
            </p>
        </section>
    </div >)
}

export default DrinksListItem