import { createReducer } from '@reduxjs/toolkit'
import {
    showCategories, chooseCategory
} from './categories-actions'
import categories from './categories-data'

export const CATEGORIES_VIEW = 'CATEGORIES_VIEW'
export const CATEGORY_VIEW = 'CATEGORY_VIEW'

const initialState = {
    view: CATEGORIES_VIEW,
    categories: categories,
    category: {}
}

// https://redux-toolkit.js.org/api/createreducer/
const categoryReducer = createReducer(initialState, {
    [showCategories]: (state, action) => {
        state.view = CATEGORIES_VIEW
        state.category = {}
    },

    [chooseCategory]: (state, action) => {
        state.view = CATEGORY_VIEW
        state.category = action.payload
    }
})

export default categoryReducer
