import React, { useEffect } from 'react'
import { connect } from "react-redux";
import './dashboard.scss'
import log from '../tools/log'
import { setAppMode } from '../app/app-actions'
import { APP_NORMAL_MODE, APP_DASHBOARD_MODE, APP_DASHBOARD_FULL_SCREEN_MODE } from '../app/app-reducer'
import { setDrinksData } from '../drinks/drinks-actions'
import { drinks } from '../drinks/drinks-mock-data'
import usbProxy from '../usb/usb'
// import { usbGetLogs } from '../usb/usb-actions'

const stateToProps =
    state => ({
        appMode: state.app.mode,
        drinksData: { drinks }
    })

const dispatchToProps =
    dispatch => ({
        setModeDashboard: mode => {
            if (mode === APP_DASHBOARD_MODE) {
                dispatch(setAppMode(APP_DASHBOARD_FULL_SCREEN_MODE))
            }

            if (mode === APP_DASHBOARD_FULL_SCREEN_MODE) {
                dispatch(setAppMode(APP_DASHBOARD_MODE))
            }
        },
        setMockDrinksData: drinksData => {
            dispatch(setDrinksData(drinksData))
        },
        clearLogs: log.clear,

        closeDashboard: () => dispatch(setAppMode(APP_NORMAL_MODE)),

        kioskOn: () => usbProxy.exec('dispatch', 'kioskOn')
            .then(log.log)
            .catch(log.err),
        kioskOff: () => usbProxy.exec('dispatch', 'kioskOff')
            .then(log.log)
            .catch(log.err),

        getUsbPluginLogs: () => usbProxy.exec('dispatch', 'getLogs')
            .then(log.log)
            .catch(log.err),
        clearUsbPluginLogs: () => usbProxy.exec('dispatch', 'clearLogs')
            .then(log.log)
            .catch(log.err),
        getUsbDeviceInfo: () => usbProxy.exec('dispatch', 'getUsbDeviceInfo')
            .then(log.log)
            .catch(log.err),
        getUsbDeviceConfig: () => usbProxy.exec('dispatch', 'getUsbDeviceConfig')
            .then(log.log)
            .catch(log.err)
    })

function Dashboard({ appMode, setModeDashboard, kioskOn, kioskOff, drinksData, setMockDrinksData, clearLogs, closeDashboard, getUsbPluginLogs, clearUsbPluginLogs, getUsbDeviceInfo, getUsbDeviceConfig }) {

    useEffect(() => {
        // your post layout code (or 'effect') here.
        log.init('dashboard-console')
        log.log('Dashboard.jsx:: dashboard-console: connected')
    },
        [])

    return <div className='dashboard'>
        <div>

            <button className='dashboard-button'
                onClick={() => closeDashboard()}>-X-</button>

            <button className='dashboard-button'
                onClick={() => kioskOn()}>
                kiosk on</button>

            <button className='dashboard-button'
                onClick={() => kioskOff()}>
                kiosk off</button>

            <button className='dashboard-button'
                onClick={() => setModeDashboard(appMode)}>
                toggle fullscreen</button>

            <button className='dashboard-button'
                onClick={() => setMockDrinksData(drinksData)}>
                load mock data</button>

            <button className='dashboard-button'
                onClick={() => clearLogs()}>
                clear</button>

            <button className='dashboard-button'
                onClick={() => clearUsbPluginLogs()}>
                clear usb logs</button>

            <button className='dashboard-button'
                onClick={() => getUsbPluginLogs()}>
                get usb logs</button>

            <button className='dashboard-button'
                onClick={() => getUsbDeviceInfo()}>
                device info</button>

            {/* <button className='dashboard-button'
                onClick={() => getUsbDeviceConfig()}>
                device config</button> */}
        </div>
        <div id='dashboard-console' className='dashboard-console'></div>
    </div>
}

export default connect(stateToProps, dispatchToProps)(Dashboard);