import { createReducer } from '@reduxjs/toolkit'
import json from '../tools/json'

import {
    usbAddMsgToDevice,
    usbMsgFromPlugin
} from './usb-actions'

const initialState = {
    dataFromUsb: [],
    dataToUsb: []
}

// https://redux-toolkit.js.org/api/createreducer/
const usbReducer = createReducer(initialState, {
    [usbAddMsgToDevice]: (state, action) => {
        state.dataToUsb.push(action.payload)
    },

    [usbMsgFromPlugin]: (state, action) => {
        state.dataFromUsb.push(json.tojson(action.payload))
    }
})

export default usbReducer
