
import { random, times } from 'lodash'

const nr = random(17, 17)

export const drinks = times(nr, i => ({
    id: 'drink-' + i,
    name: 'drink ' + i,
    desc: getLorems()['' + (i === 0 ? 0 : i % 9)],
    ingredients: getIngredients()['' + (i === 0 ? 0 : i % 9)],
    icon: 'icon.png'
}))

function getIngredients() {
    return {
        '0': ['vodka', 'asdf', 'qwer'],
        '1': ['jin', 'asdfqwerqwe', 'qwer', 'qwerqwr qwerq'],
        '2': ['rum', 'asdf', 'qwer', 'qwerqwerqwerqew'],
        '3': ['milk', 'asdf', 'qwer'],
        '4': ['wodka', 'asdf', 'qwer'],
        '5': ['jin', 'asdf', 'qwer'],
        '6': ['rum', 'asdf', 'qwer'],
        '7': ['milk', 'asdf', 'qwer'],
        '8': ['vodka', 'asdf', 'qwer'],
        '9': ['tonic', 'asdf', 'qwer']
    }
}

