import { configureStore, getDefaultMiddleware } from '@reduxjs/toolkit'
import appReducer from './app-reducer'
import categoriesReducer from '../categories/categories-reducer'
import drinksReducer from '../drinks/drinks-reducer'
import usbReducer from '../usb/usb-reducer'
import usbMiddleware from '../usb/usb-middleware'
import loggingMiddleware from '../tools/loggingMiddleware'

// https://redux-toolkit.js.org/usage/usage-guide/
// on addition, configureStore adds some middleware by default, each with a specific goal:
// - redux-thunk is the most commonly used middleware for working with both synchronous and async logic outside of components
// - In development, middleware that check for common mistakes like mutating the state or using non-serializable values.
const store = configureStore({
    reducer: {
        app: appReducer,
        categories: categoriesReducer,
        drinks: drinksReducer,
        usb: usbReducer
    },
    middleware: [
        loggingMiddleware,
        usbMiddleware,
        ...getDefaultMiddleware()
    ],
})

// store.subscribe(o => console.log('store: o = ' + o));
// window.store = store;

export default store;