const console = window.console;

let c = null
let count = 0
let logs = []

function logOnConsole(msg) {
    count++
    c.innerText = count + '.\t'
        + msg
        // + msg.toString().replace(/,/g, '\r\n')
        + '\r\n'
        + c.innerText;
}

function create(console) {
    return {
        init: id => {
            c = document.getElementById(id)
            count = -1
            logOnConsole('console OK')
            logs.forEach(logOnConsole)
        },

        log: msg => {
            console.log(msg)
            logs.push(msg)
            if (c) {
                logOnConsole(msg)
            }
        },

        err: msg => {
            console.error(msg)
            const err = "_ERR_: " + msg
            logs.push(err)
            if (c) {
                logOnConsole(err)
            }
        },

        clear: () => {
            if (c) {
                c.innerText = '';
                count = 0
                logs = []
            }
        }
    }
}

const instance = create(console)

export {
    instance as default,
    create
}