import { batch } from 'react-redux';
import usbDevice from './usb'
import { setAppState, showApp } from '../app/app-actions'
import { usbMsgFromPlugin } from './usb-actions'
import { APP_USB_GUI_CONNECTED_STATE, APP_USB_DEVICE_CONNECTED_STATE } from '../app/app-reducer'
import { setDrinksData, resetDrinks } from '../drinks/drinks-actions'
import { drinks } from '../drinks/drinks-mock-data'

const usbMiddleware = store => next => action => {
    // console.log(Promise.resolve(action) === action)

    if (usbMsgFromPlugin.type === action.type) {

        if (action.payload === 'guiConnected') {
            store.dispatch(setAppState(APP_USB_GUI_CONNECTED_STATE))
        }

        if (action.payload === 'deviceConnected') {
            store.dispatch(setAppState(APP_USB_DEVICE_CONNECTED_STATE))
        }

        if (action.payload === 'setData') {
            batch(() => {
                store.dispatch(setDrinksData(drinks))
                store.dispatch(showApp())
            })
        }

        if (action.payload === 'getData') {
            const state = store.getState()
            let msg = 'none'
            if (state.usb.dataToUsb.length) {
                msg = state.usb.dataToUsb.join(',')

                usbDevice.exec('write', msg)
                    .then(store.dispatch(resetDrinks()))
            }
        }

        if (action.payload === 'drinkDone') {
            store.dispatch(showApp())
        }

        return
    }

    next(action);
}

export default usbMiddleware;