import React, { useEffect } from 'react'
import { connect } from "react-redux";
import './app/_app-variables.scss'
import './app/_app.scss'
import './App.scss'
import {
  APP_DISPLAY_DRINKER, APP_DISPLAY_AUX,
  APP_VIEW, APP_LOADING_VIEW,
  APP_NORMAL_MODE, APP_DASHBOARD_MODE, APP_DASHBOARD_FULL_SCREEN_MODE
} from './app/app-reducer'
import AppLoading from './app/AppLoading'
import Categories from './categories/CategoriesView'
import DrinkerAux from './drinker-aux/DrinkerAux'
import Dashboard from './dashboard/Dashboard'
import tmkusbpluginMock from './usb/tmkUsbPlugin-mock'
import usbProxy from './usb/usb'
import { usbMsgFromPlugin } from './usb/usb-actions'
// import log from './tools/log'

// const console = window.console

const appViews = {
  APP_VIEW: Categories,
  APP_LOADING_VIEW: AppLoading
}

const stateToProps =
  state => {
    const AppView = state.app.display === APP_DISPLAY_DRINKER
      ? Categories
      : DrinkerAux

    return ({
      appMode: state.app.mode,
      AppView
    })
  }

const dispatchToProps =
  dispatch => ({
    usbMsgFromDevice: msg => dispatch(usbMsgFromPlugin(msg))
  })

/**
 * cordova set in index.html because here is not present in window
 * 
 */
function App({ cordova, AppView, appMode, usbMsgFromDevice }) {

  useEffect(
    () => {
      // usbProxy.addOnReadSucClbk('console', console.log);
      // usbProxy.addOnReadSucClbk('log', log.log);
      usbProxy.addOnReadSucClbk('action', usbMsgFromDevice);
      // usbProxy.addOnReadErrClbk('console', console.error);
      // usbProxy.addOnReadErrClbk('log', log.err);
      usbProxy.addOnReadErrClbk('action', usbMsgFromDevice);
      if (cordova) {
        usbProxy.connectGui(cordova.plugins.tmkusb)
      } else {
        usbProxy.connectGui(tmkusbpluginMock)

        // load mock drink data
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []);

  if (appMode === APP_NORMAL_MODE) {
    return <AppView />
  }

  if (appMode === APP_DASHBOARD_MODE) {
    return (
      <div id="app">
        <div id="app-view-wrapper"><AppView /></div>
        <div id="app-dashboard-wrapper"><Dashboard /></div>
      </div>
    )
  }

  if (appMode === APP_DASHBOARD_FULL_SCREEN_MODE) {
    return (
      <Dashboard />
    )
  }

  return <div>no proper mode</div>
}

export default connect(stateToProps, dispatchToProps)(App);
